@import "../../styles/global.scss";

.hero {
  background-color: $color-grey;
  padding: 170px 150px;
  .hero-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    .title {
      font-size: 90px;
      font-weight: 600;
      margin: 2px 0;
    }
    .subtitle {
      font-size: 40px;
      font-weight: 300;
      margin: 2px 0;
    }
    @include break-point(mobile) {
      .title {
        font-size: 2.5rem;
      }
      .subtitle {
        font-size: 1.5rem;
      }
    }
    .circle {
      width: 350px;
      height: 350px;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.786);
      box-shadow: 0px 0px 126px 11px rgba(29, 83, 255, 0.1);
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        position: absolute;
        -webkit-animation: spin 20s linear infinite;
        -moz-animation: spin 20s linear infinite;
        animation: spin 20s linear infinite;
      }
      .emoji {
        position: absolute;
        font-size: 90px;
      }
      @include break-point(mobile) {
        width: 220px;
        height: 220px;
        img {
          width: 190px;
        }
        .emoji {
          font-size: 60px;
        }
      }
    }
    .row {
      justify-content: space-between;
    }
    @media (max-width: 1200px) {
      grid-template-columns: 1fr;
      row-gap: 100px;
      .end,
      .col {
        justify-content: center;
        text-align: center;
      }
    }
  }
  @include break-point(mobile) {
    padding: 150px 25px 50px;
  }
}

@-moz-keyframes spin {
    100% {
      -moz-transform: rotate(360deg);
    }
  }
  @-webkit-keyframes spin {
    100% {
      -webkit-transform: rotate(360deg);
    }
  }
  @keyframes spin {
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  